footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: 80px;
  background: #111622;
  box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.15); }
  footer ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
    height: 100%; }
    footer ul li {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 1rem;
      transition: color 0.25s;
      width: 8rem;
      height: 6rem;
      box-sizing: border-box;
      padding-bottom: 1.6rem; }
      footer ul li.active {
        position: relative; }
        footer ul li.active img {
          width: 2rem;
          height: 2rem; }
        footer ul li.active i {
          transition: color 0.25s;
          color: #FFFFFF; }
      footer ul li.active:after {
        content: '0';
        font-size: 0;
        width: 100%;
        height: 1px;
        background-color: #E32A2A;
        position: absolute;
        top: -18px;
        left: 0; }
      footer ul li img {
        width: 2rem;
        height: 2rem;
        margin-bottom: .5rem; }
      footer ul li i {
        margin: 0;
        padding: 0;
        list-style: none;
        font-style: normal;
        font-size: 14px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #6D778B; }
